import React from 'react';
import rameel from './rameel.png';
import Tilt from 'react-tilt';
import resume from './Profile.pdf';

const About =()=>{

	return(
          <div>
              <article className="mw6 shadow-5 o-80 center bg-white br3 pa4 pa4-ns mv3 ba b--black-10">
                        <div className="tc">
                             <Tilt className="Tilt center" options={{ max : 85 }} style={{height:125, width:100}}>
                                <div className="Tilt-inner center">
                                   <img alt="rameel" src={rameel} className="br-100 pa1 ba b--black-50 h4 w4 dib" />
                                </div>
                             </Tilt>
                             <h1 className="f3">{'Rameel'}</h1>
                             <hr className="mw3 bb bw1 b--black-10" />
                        </div> 
                        <p className="lh-copy measure center f6 black-70 animated fadeInRight">
                          {'I\'m a full-stack developer actively seeking to establish a career building web applications and services.'}
                          {'My true passion is to work on projects that provide an impactful solution to real-world problems '}
                        </p> 
                         <div className="ph3 center grow dim">
                         <a className="br-pill ph3 pv2 white dib bg-navy shadow-5  animated fadeInUp infinite" href={resume}>Download Resume</a>
                         </div>           
              </article>
          </div>
		)
}
export default About;